.spotlight {
  &.spotlight-testimony {
    background-color: $N01;
    .spot-tbl {
      .spot-tbl-row .spot-content {
        background-color: $N01;
        .h-style {
          color: $S01;
          @include fsB03;
        }
        p {
          @include fsR02;
          @include fixLines(4, 31px);
          @media screen and (max-width: $screen-xs) {
            @include fsMobile-R03;
            @include fixLines(4, 24px, 4);
          }
        }
      }
    }
  }
}