footer {
  background-color: $P03;
  padding-top: 60px;
  padding-bottom: 1px;
  @media screen and (max-width: $screen-xs) {
    padding-top: 30px;
  }
  * {
    color: $N01;
  }
  //TODO: remove h4
  h4,
  .h4 {
    color: $N01;
    @include fsB03;
    display: block;
    margin-bottom: 26px;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B03;
      margin-bottom: 20px;
    }
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: block;
    font-size: 0;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 20px;
    }
    li {
      padding: 9px 0;
      display: inline-block;
      width: 100%;
      @media screen and (max-width: $screen-xs) {
        padding: 10px 0;
        width: 50%;
      }
      a {
        color: $N01;
        @include fsR03;
        display: inline-block;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-R03;
        }
        &:hover,
        &:focus,
        &:active {
          color: $N01;
        }
      }
      &:first-of-type {
        @media screen and (min-width: $screen-sm) {
          padding-top: 0;
        }
      }
      &:last-of-type {
        @media screen and (min-width: $screen-sm) {
          padding-bottom: 0;
        }
      }
    }
  }
  .last-row {
    padding: 70px 0 40px;
    @media screen and (max-width: $screen-xs) {
      padding: 30px 0;
    }
    ul {
      li {
        padding: 0 15px;
        width: auto;
        @media screen and (max-width: $screen-xs) {
          width: 50%;
          padding: 10px 0;
        }
        a {
          @include fsB05;
          @media screen and (max-width: $screen-xs) {
            @include fsMobile-B05;
          }
        }
        &:first-of-type {
          padding-left: 0;
        }
        &:last-of-type {
          padding-right: 0;
        }
      }
    }
    .text-right {
      @media screen and (max-width: $screen-xs) {
        text-align: left !important;
      }
      p {
        @include fsR04;
        margin: 0;
      }
    }
  }
}

.compliance-text *,
.compliance-text {
  display: block;
  @include x-rem("font-size",14px);
  line-height: 1.6;
}