a.spotlight {
  color: $N04;
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .spot-content {
      .spot-title {
        text-decoration: none;
      }
    }
  }
}

.spotlight {
  background-color: $white;
  @media screen and (max-width: $screen-xs) {
    background-color: transparent;
  }
  .spot-content {
    .spot-label {
      font-size: 0.75rem;
      line-height: 16px;
      font-weight: 700;
      > * {
        color: inherit;
      }
    }
    .spot-title {
      color: $S01;
      @include fsB04;
      @include fixLines(2, 28px);
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B03;
        @include fixLines(2, 30px, 2);
      }
    }
    p {
      @include fixLines(4, 24px);
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R03;
        @include fixLines(4, 24px, 4);
      }
    }
  }
}

.columns-4 {
  > div[class*="col-sm"] > .buttons-wrapper {
    a:nth-of-type(2) {
      @media screen and (min-width: $screen-sm) {
        margin-left: 0;
        margin-top: 20px;
      }
    }
  }
}


.columns-3 {
  box-sizing: border-box;
}
.columns-2 > div[class*="col-sm"] > .spotlight {
  .spot-content {
    .spot-title {
      @media screen and (min-width: $screen-sm) {
        @include fsB03;
        @include fixLines(2, 40px, 2);
      }
    }
  }
}
