/*Use these class with @extend i.e

.sample-box {
  @extend .svg-social_facebook
  &:after {
  @extend .svg-social_facebook
  }
}

Where ever you want to use it, because its give benefit to easily add new OR remove icons from sprite
just replace your svg sprite and modify these classes.

*/
@mixin svg-arrow-back {
  background: url("svg/sprite-1.svg") 36.36363636363637% 98.95833333333333% no-repeat;
  width: 12px;
  height: 21px;
}

@mixin svg-arrow-down-green {
  background: url("svg/sprite-1.svg") 99.15254237288136% 39.25233644859813% no-repeat;
  width: 15px;
  height: 10px;
}

@mixin svg-arrow-down-white {
  background: url("svg/sprite-1.svg") 99.15254237288136% 29.906542056074766% no-repeat;
  width: 15px;
  height: 10px;
}

@mixin svg-arrow-forward {
  background: url("svg/sprite-1.svg") 46.28099173553719% 98.95833333333333% no-repeat;
  width: 12px;
  height: 21px;
}

@mixin svg-arrow-right {
  background: url("svg/sprite-1.svg") 96.85039370078741% 48.598130841121495% no-repeat;
  width: 6px;
  height: 10px;
}

@mixin svg-arrow-right-green {
  background: url("svg/sprite-1.svg") 92.1259842519685% 48.598130841121495% no-repeat;
  width: 6px;
  height: 10px;
}

@mixin svg-chevron-right-black {
  background: url("svg/sprite-1.svg") 99.23076923076923% 46.846846846846844% no-repeat;
  width: 3px;
  height: 6px;
}

@mixin svg-chevron-right-green {
  background: url("svg/sprite-1.svg") 90% 55.85585585585586% no-repeat;
  width: 3px;
  height: 6px;
}

@mixin svg-chevron-right-white {
  background: url("svg/sprite-1.svg") 92.3076923076923% 55.85585585585586% no-repeat;
  width: 3px;
  height: 6px;
}

@mixin svg-close {
  background: url("svg/sprite-1.svg") 19.81981981981982% 100% no-repeat;
  width: 22px;
  height: 22px;
}

@mixin svg-close-grey {
  background: url("svg/sprite-1.svg") 24.770642201834864% 70.96774193548387% no-repeat;
  width: 24px;
  height: 24px;
}

@mixin svg-download {
  background: url("svg/sprite-1.svg") 0 75% no-repeat;
  width: 27px;
  height: 29px;
}

@mixin svg-external-link-black {
  background: url("svg/sprite-1.svg") 100% 15.841584158415841% no-repeat;
  width: 16px;
  height: 16px;
}

@mixin svg-external-link-green {
  background: url("svg/sprite-1.svg") 74.35897435897436% 94.05940594059406% no-repeat;
  width: 16px;
  height: 16px;
}

@mixin svg-external-link-white {
  background: url("svg/sprite-1.svg") 100% 0 no-repeat;
  width: 16px;
  height: 16px;
}

@mixin svg-facebook {
  background: url("svg/sprite-1.svg") 86.88524590163935% 0 no-repeat;
  width: 11px;
  height: 22px;
}

@mixin svg-instagram {
  background: url("svg/sprite-1.svg") 59.64912280701754% 96.93877551020408% no-repeat;
  width: 19px;
  height: 19px;
}

@mixin svg-menu {
  background: url("svg/sprite-1.svg") 64.76190476190476% 38.38383838383838% no-repeat;
  width: 28px;
  height: 18px;
}

@mixin svg-play-icon {
  background: url("svg/sprite-1.svg") 0 0 no-repeat;
  width: 68px;
  height: 66px;
}

@mixin svg-search {
  background: url("svg/sprite-1.svg") 71.57894736842105% 0 no-repeat;
  width: 38px;
  height: 38px;
}

@mixin svg-search-mobile {
  background: url("svg/sprite-1.svg") 0 100% no-repeat;
  width: 22px;
  height: 22px;
}

@mixin svg-twitter {
  background: url("svg/sprite-1.svg") 64.60176991150442% 66.66666666666667% no-repeat;
  width: 20px;
  height: 18px;
}

@mixin svg-youtube {
  background: url("svg/sprite-1.svg") 45.945945945945944% 66.66666666666667% no-repeat;
  width: 22px;
  height: 18px;
}

