
// Bootstrap overrides
//
// Copy variables from `_variables.scss` to this file to override default values
// without modifying source files.


//
// Color system
//
$primary: #00A76A;
$secondary: #FFC845;
$success: #F47920;
$info: #89764B;
$warning: #55D1FF;
$danger: #EE7B7E;
$light: #C6C6C6;
$dark: #424242;
$theme-colors: ();

$theme-colors: map-merge((
  "primary":    #00A76A,
  "secondary":  #FFC845,
  "success":    #F47920,
  "info":       #89764B,
  "warning":    #55D1FF,
  "danger":     #EE7B7E,
  "light":      #C6C6C6,
  "dark":       #424242
), $theme-colors);
//Remove color from theme
//$theme-colors: map-remove($theme-colors, "danger");
// Grid breakpoints

$grid-breakpoints: (
        xs: 0,
        sm: 641px,
        md: 641px,
        lg: 641px,
        xl: 641px
);

// Grid containers

$container-max-widths: (
        sm: 1200px,
        md: 1200px,
        lg: 1200px,
        xl: 1200px
);

$grid-gutter-width: 30px;


// Body
//
// Settings for the `<body>` element.;
$body-bg: #FFFFFF;
$body-color: $dark;
$link-color: #118657;//$P03
$link-hover-color: #118657;//$P03
$link-hover-decoration: underline;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.;
$font-family-base: 'Poppins', sans-serif , monospace;
$font-size-base: 1rem;
$h1-font-size: 2.25rem;
$h2-font-size: 1.875rem;
$h3-font-size: 1.5rem;
$h4-font-size: 1.125rem;
$h5-font-size: 0.875rem;
$h6-font-size: 0.75rem;
$line-height-base: 1.6;
$headings-font-family: 'ap-sans', sans-serif, monospace;
$headings-font-weight: 700;
$headings-line-height: 1.1;
$headings-color: $dark;
$border-radius: 4px;

// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.;
$table-cell-padding: 20px;
$table-color: #424242;
$table-bg: transparent;
$table-accent-bg: #F4F4F4;
$table-hover-bg: #C6C6C6;
$table-active-bg: transparent;
$table-border-color: transparent;
$table-border-width: 1px;

// Buttons + Forms

$btn-font-weight: normal;
$btn-padding-y:               20px;
$btn-padding-x:               45px;
$input-bg: #FFFFFF;
$input-color: #333333;
$input-border-color: #333333;
$input-border-radius: 5px;
$input-border-radius-lg: 5px;
$input-border-radius-sm: 5px;
$input-focus-border-color: #66AFE9;
$btn-border-width: 2px;
$btn-border-radius: 3px;
// Dropdowns
//
// Dropdown menu container and contents.
$dropdown-min-width: 0;
$dropdown-bg: #FFFFFF;
$dropdown-border-color: transparent;
$dropdown-divider-bg: transparent;
$dropdown-link-color: #03A6FF;
$dropdown-link-hover-color: #79a3c2;
$dropdown-link-hover-bg: transparent;
$dropdown-link-active-color: #03A6FF;
$dropdown-link-active-bg: transparent;

// Breadcrumbs
$breadcrumb-padding-y: 12px;
$breadcrumb-padding-x: 0px;
$breadcrumb-item-padding: 8px;
$breadcrumb-margin-bottom: 0px;
$breadcrumb-bg: transparent;
$breadcrumb-divider-color: $dark;
$breadcrumb-active-color: $dark;
$breadcrumb-divider: "/";
$breadcrumb-border-radius: 0;


// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret: true;
$enable-rounded: true;
$enable-shadows: false;
$enable-gradients: false;
$enable-transitions: true;
$enable-hover-media-query: false;
$enable-grid-classes: true;
$enable-print-styles: true;


// Pagination
$pagination-padding-y:              5px;
$pagination-padding-x:              15px;

$pagination-color:                  $primary;
$pagination-bg:                     transparent;
$pagination-border-width:           0;
$pagination-border-color:           transparent;

$pagination-focus-box-shadow:       none;
$pagination-focus-outline:          none;

$pagination-hover-color:            #FFFFFF;
$pagination-hover-bg:               $primary;
$pagination-hover-border-color:     $primary;

$pagination-active-color:           #FFFFFF;
$pagination-active-bg:              $primary;
$pagination-active-border-color:    $primary;

$pagination-disabled-color:         #C6C6C6;
$pagination-disabled-bg:            transparent;
$pagination-disabled-border-color:  transparent;

// Cards

$card-spacer-y:                     0;
$card-spacer-x:                     0;
$card-border-width:                 2px;
$card-border-radius:                0;
$card-border-color:                 #F4F4F4;
$card-inner-border-radius:          0;
$card-cap-bg:                       #FFFFFF;
$card-bg:                           #FFFFFF;


// Badges

$badge-font-size:                   75% !default;
$badge-font-weight:                 $font-weight-bold !default;
$badge-padding-y:                   15px;
$badge-padding-x:                   15px;
$badge-border-radius:               3px;

$badge-transition:                  $btn-transition !default;
$badge-focus-width:                 $input-btn-focus-width !default;

$badge-pill-padding-x:              15px;
// Use a higher than normal value to ensure completely rounded edges when
// customizing padding or font-size on labels.
$badge-pill-border-radius:          3px;


// Carousel
$carousel-indicator-width:           15px;
$carousel-indicator-height:          15px;
$carousel-indicator-active-bg:        transparent;

// Navs

$nav-link-padding-y:                0;
$nav-link-padding-x:                0;