.social-links-list {
  list-style: none;
  padding: 0;
  margin: 0 0 30px;
  display: inline-block;
  li {
    display: inline-block;
    width: auto;
    padding: 0;
    a {
      display: inline-block;
      width: 48px;
      height: 48px;
      border: 1px solid $N01;
      position: relative;
      margin-right: 15px;
      &:before {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        content: "";
        opacity: 0.8;
      }
      &[href*="facebook"]:before {
        @include svg-facebook;
      }
      &[href*="twitter"]:before {
        @include svg-twitter;
      }
      &[href*="youtube"]:before {
        @include svg-youtube;
      }
      &[href*="instagram"]:before {
        @include svg-instagram;
      }
      &:hover,
      &:focus,
      &:active {
        &:before {
          opacity: 1;
        }
      }
    }
    &:last-of-type {
      a {
        margin-right: 0;
      }
    }
  }
}

.breadcrumb {
  visibility: visible;
  display: block;
  .breadcrumb-item {
    display: inline;
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (max-width: $screen-xs) {
      padding-left: 10px;
      padding-right: 10px;
    }
    @include fsR04;
    a {
      text-decoration: underline;
      display: inline;
      color: $N04;
      @include fsR04;
    }
    + .breadcrumb-item {
      position: relative;
      &:before {
        @include svg-arrow-right-green;
        padding: 0;
        content: "";
        display: block;
        position: absolute;
        top: 5px;
        right: auto;
        bottom: auto;
        left: -4px;
        margin: auto;
      }
    }
    &:first-of-type {
      padding-left: 0;
    }
    &:last-of-type {
      padding-right: 20px;
    }
  }
}

.pagination {
  visibility: visible;
  @media screen and (max-width: $screen-xs) {
    justify-content: center;
  }
  .page-item {
    .page-link {
      border-radius: 3px;
      @include fsR02;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R03;
      }
    }
    &.active {
      .page-link {
        padding: 5px 17px;
      }
    }
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
    &:first-of-type,
    &:last-of-type,
    &.active {
      display: flex;
    }
  }
}

.carousel  {
  .carousel-indicators {
    li {
      border: 1px solid $N03;
      opacity: 1;
      border-radius: 50%;
      &.active {
        background-color: $N03;
      }
    }
  }
}

.nav-sitemap {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  visibility: visible;
  li {
    width: 100%;
    display: block;
    a {
      display: inline-block;
      width: auto;
    }
    > ul {
      //second level
      margin-left: 15px;
      visibility: visible;
      padding-top: 10px;
    }
  }
}