.accordion {
  .card {
    border: none;
    .card-header {
      position: relative;
      border-top: 2px solid $N02;
      border-bottom: 2px solid $N02;
      .card-link {
        position: relative;
        display: inline-block;
        width: 100%;
        @include fsM03;
        color: $P03;
        padding: 15px 40px 15px 20px;
        border: none;
        &:after {
          content: "";
          position: absolute;
          display: block;
          margin: auto;
          top: 0;
          right: 20px;
          bottom: 0;
          left: auto;
          @include svg-arrow-down-green;
        }
        &[aria-expanded="true"] {
          &:after {
            transform: rotate(180deg);
          }
        }
      }
    }
    .card-body {
      padding: 30px 20px;
      *:last-child {
        margin-bottom: 0;
      }
    }
    &:last-of-type {
      .card-header {
        margin-bottom: 0;
      }
    }
  }
}