a.spotlight {
  display: block;
  &:hover,
  &:focus,
  &:active {
    outline: none;
  }
}
.spotlight {
  display: block;
  .image-wrapper {
    display: block;
    position: relative;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0;
  }
  .spot-content {
    padding: 20px 10px;
    .spot-label {
      display: inline-block;
    }
    .spot-title {
      display: block;
      margin-bottom: 12px;
    }
    .line {
      display: inline-block;
      height: 3px;
      width: 113px;
      background: $G03;
      margin-bottom: 12px;
    }
    p {
      display: block;
      margin: 0;

    }
  }
  &.with-buttons {
    margin-bottom: 0;
    + .buttons-wrapper {
      padding: 20px 10px 0;
      @media screen and (max-width: $screen-xs) {
        padding-top: 0;
      }
      a {
        margin-left: 40px;
        @media screen and (max-width: $screen-xs) {
          margin-left: 15px;
        }
        &:first-of-type {
          margin-left: 0;
          @media screen and (max-width: $screen-xs) {
            margin-left: 0;
          }
        }
      }
    }
  }
}

@import "sub_imports";