$videoHeight: 610px;
$mobileHeight: 630px;
.media-banner {
  padding: 0;
  position: relative;
  height: $videoHeight;
  background: $G03;
  @media screen and (max-width: $screen-xs) {
    height: $mobileHeight;
    overflow: hidden;
  }
  img {
    display: block;
    width: auto;
    height: auto;
    margin: 0;
  }

  .media-container {
    position: relative;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    max-height: $videoHeight;
    @media screen and (max-width: $screen-xs) {
      max-height: $mobileHeight;
    }
    video {
      position: absolute;
      display: block;
      width: auto;
      height: auto;
      margin: auto;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 2;
    }
    .poster {
      z-index: 1;
    }
  }

  .content-box-wrapper {
    position: relative;
    @media screen and (max-width: $screen-xs) {
      position: inherit;
    }
    .content-box {
      position: absolute;
      top: 95px;
      right: auto;
      bottom: auto;
      left: 0;
      margin: auto;
      display: block;
      z-index: 3;
      min-width: 550px;
      max-width: 550px;
      min-height: 380px;
      padding: 25px;
      border-radius: 0;
      background-color: $P01;
      @include dropShadowMainBanner;
      @media screen and (max-width: $screen-xs) {
        padding: 20px;
        top: 300px;
        right: 0;
        bottom: auto;
        left: 0;
        min-width: 1px;
        max-width: calc(100% - 30px);
        min-height: 240px;
        @include dropShadowMainBannerBox;
      }
      a.btn {
        min-width: 200px;
        margin-right: 30px;
        @media screen and (max-width: $screen-xs) {
          min-width: 100%;
          margin-right: 0;
          margin-bottom: 15px;
        }
      }
      .h-style {
        display: block;
        @include fsB02;
        color: $N01;
        margin-top: 30px;
        margin-bottom: 20px;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-B02;
        }
      }
      .intro {
        display: block;
        color: $N01;
        @include fsR02;
        margin-bottom: 15px;//40px;
        letter-spacing: normal;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-R03;
        }
      }
      .terms {
        display: block;
        color: $N01;
        @include fsR04;
        margin-top: 50px;
        @media screen and (max-width: $screen-xs) {
          margin-top: 30px;
        }
      }
    }
  }
  &.landing-banner {
    background-color: $N01;
    @media screen and (max-width: $screen-xs) {
      height: 510px;
    }
    &:before {
      position: absolute;
      top: 0;
      right: auto;
      bottom: 0;
      left: 0;
      display: block;
      margin: auto;
      content: "";
      width: 35vw;
      height: 100%;
      background-color: $N01;
      z-index: 1;
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
    .media-container {
      background: $G03;
      @media screen and (max-width: $screen-xs) {
        background: $N01;
      }
      .centered-image img {
        @media screen and (min-width: $screen-sm) {
          margin-left: 50%;
          left: auto !important;//100% !important;
          transform: none !important;//translateX(-100%);
        }
      }
    }
    .content-box-wrapper {
      &:before {
        display: block;
        position: absolute;
        top: 0;
        right: auto;
        bottom: auto;
        left: 42px;
        margin: auto;
        content: "";
        background: url("imgs/product-banner-bg.png") no-repeat 50% center;
        width: 960px;
        height: 611px;
        z-index: 1;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
          height: 230px;
          background: url("imgs/product-banner-mobile-bg.png") no-repeat right top;
          background-size: cover;
          top: 110px;
          right: 0;
          bottom: auto;
          left: 0;
        }
      }
    }
    .content-box-wrapper .content-box {
      background-color: transparent;
      box-shadow: none;
      @media screen and (max-width: $screen-xs) {
        box-shadow: none;
        top: 260px;
      }
      .terms {
        color: $N04;
      }
      .intro {
        @include fsB04;
        color: $N04;
        margin-bottom: 10px;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-B06;
        }
      }
      .h-style {
        color: $S01;
        @include fsB01;
        margin-top: 0;
        margin-bottom: 15px;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-B02;
        }
      }
    }
  }
}
.media-banner.terms-banner {
  @media screen and (max-width: $screen-xs) {
    height: 745px;
    &.landing-banner {
      height: 630px;
    }
  }
}

