//TODO: This should be optimized later on
a.spotlight-card {
  display: block;
  border-top: 10px solid $P01;
  position: relative;
  &:before {
    content: "";
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(2, 2, 2, 0.5) 100%);
    width: 100%;
    height: 225px;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
  }
  .image-wrapper {
    display: block;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
    img {
      width: 100%;
      margin-bottom: 0;
    }
  }
  .spot-content {
    position: absolute;
    bottom: 0;
    left: 0;
    max-width: 100%;
    padding: 0 15px 20px;
    z-index: 2;
    .spot-tag {
      @include fsB06;
      color: $white;
    }
    .spot-title {
      color: $white;
      @include fsB04;
      @include fixLines(3, 28px, 0, 1);
    }
  }
  &:hover,
  &:focus {
    text-decoration: none;
    .spot-content {
      .spot-title {
        text-decoration: underline;
      }
    }
  }
}

a.spotlight,
a.spotlight-card {
  &:hover,
  &:focus {
    outline: none;
    @include dropShadowSpotlight;
  }
}