.spotlight {
  &.spotlight-testimony {
    position: relative;
    .spot-tbl {
      display: table;
      width: 100%;
      .spot-tbl-row .image-wrapper,
      .spot-tbl-row .spot-content {
        display: table-cell;
        vertical-align: top;
        position: relative;
        @media screen and (max-width: $screen-xs) {
          display: block;
        }
      }
      .spot-tbl-row .image-wrapper {
        width: 40%;
        max-width: 40%;
        float: left;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
          max-width: 100%;
          float: inherit;
        }
      }
      .spot-tbl-row .image-wrapper img {
        display: block;
        width: 100%;
        height: auto;
        margin: 0;
      }
      .spot-tbl-row .spot-content {
        width: 60%;
        min-width: 60%;
        max-width: 60%;
        padding: 47px 60px;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
          max-width: 100%;
          min-width: 100%;
          padding: 25px 15px;
        }
        p {
          margin-bottom: 30px;
        }
        .h-style {
          display: block;
          margin: 0 0 10px;
        }
      }
    }
    //&:nth-child(even),
    &.right-align {
      display: block;
      .spot-tbl{
        .spot-tbl-row .image-wrapper {
          float: right;
          @media screen and (max-width: $screen-xs) {
            float: inherit;
          }
          &:before{
            left: 0;
          }
        }
      }
    }
  }
}
