*[class*="svg-"] {
  display: inline-block;
}
.product-thumb {
  background-color: transparent;
  @media screen and (max-width: $screen-xs) {
    display: block;
  }
  .media-item {
    margin-right: 30px;
    margin-bottom: 0;
  }
  .media-body {
    .title {
      @include fsB04;
      color: $N06;
      display: block;
      margin-bottom: 10px;
    }
    p {
      margin-bottom: 0;
    }
  }
}


.search-result {
  display: block;
  padding-bottom: 30px;
  border-bottom: 1px solid $N02;
  .title {
    @include fsB03;
    color: $N06;
    display: block;
    margin: 0 0 23px;
  }
  p {
    margin: 0;
  }
}

.search-heading {
  display: block;
  padding-bottom: 20px;
  border-bottom: 1px solid $S01;
  .title {
    @include fsM02;
    display: block;
    margin: 0;
  }
  p {
    margin: 0;
  }
}

.featured-blog-item {
  background-color: transparent;
  .media-column {
    a {
      color: transparent;
    }
    .video-wrap {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    img {
      margin: 0;
      display: block;
      width: 100%;
      height: auto;
    }
  }
  .content-column {
    padding-top: 47px;
    padding-bottom: 47px;
    @media screen and (max-width: $screen-xs) {
      padding: 25px 15px;
    }
    .tag {
      color: $N04;
      display: block;
      @include fsB04;
      margin-bottom: 16px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 15px;
      }
    }
    .title {
      color: $S01;
      display: block;
      @include fsB03;
      margin-bottom: 10px;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B02;
        margin-bottom: 15px;
      }
    }
    p {
      display: block;
      @include fsR02;
      margin-bottom: 40px;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R03;
        margin-bottom: 15px;
      }
    }
  }
}

.video-container {
  .featured-blog-item {
    .content-column {
      padding-top: 0;
      padding-bottom: 0;
      &.offset-sm-5 {
        margin-bottom: 30px;
      }
    }
  }
}