//a:not(.custom)
a {
  display: inline-block;
  /*@include fsB06(0);
  display: inline-block;
  &:before {
    display: inline-block;
    content: "I am a Link";
    background-color: red !important;
  }*/
  &.cta-link {
    @include fsB07;
    position: relative;
    margin-right: 6px;
    border-bottom: 2px solid transparent;
    @media screen and (max-width: $screen-xs) {
      @include fsB06;
    }
    &:after {
      position: absolute;
      top: 0;
      right: -6px;
      bottom: 0;
      left: auto;
      display: block;
      margin: auto;
      content: "";
      @include svg-chevron-right-green;
    }
    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
      border-bottom-color: $P03;
    }
  }
  &.download-link {
    background-color: $N01;
    border-radius: 3px;
    border: 1px solid $N03;
    color: $N04;
    display: inline-block;
    width: 100%;
    padding: 11px 15px;
    font-size: 0;
    .icon-box {
      display: inline-block;
      width: 50px;
      padding: 0 20px 0 3px;
      vertical-align: top;
      @media screen and (min-width: $screen-sm) {
        margin-bottom: -5px;
      }
      @media screen and (max-width: $screen-xs) {
        padding-top: 12px;
      }
    }
    .text-wrapper {
      width: calc(100% - 50px);
      display: inline-block;
      .title,
      .info {
        @include fsM03;
        display: inline-block;
        text-align: left;
        vertical-align: top;
        @media screen and (max-width: $screen-xs) {
          display: block;
        }
      }
      .title {
        @media screen and (min-width: $screen-sm) {
          width: calc(100% - 150px);
        }
      }
      .info {
        @media screen and (min-width: $screen-sm) {
          width: 150px;
          text-align: right;
        }
      }
    }
    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
      .text-wrapper * {
        text-decoration: underline;
      }
    }
  }
}

.menu-card {
  a {
    &.cta-link {
      color: $N04;
      @media screen and (max-width: $screen-xs) {
        color: $P01;
      }
      &:after {
        @include svg-chevron-right-black;
        @media screen and (max-width: $screen-xs) {
          @include svg-chevron-right-green;
        }
      }
      &:hover,
      &:focus,
      &:active {
        color: $N04;
        border-bottom-color: $N04;
        @media screen and (max-width: $screen-xs) {
          color: $P01;
          border-bottom-color: $P01;
        }
      }
    }
  }
}
.media-banner:not(.landing-banner),
.bg-primary {
  a {
    &.cta-link {
      color: $N01;
      &:after {
        @include svg-chevron-right-white;
      }
      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
        border-bottom-color: $N01;
      }
    }
  }
}
