a {
  &.skip-content {
    position: absolute;
    width: 0;
    height: 0;
    padding: 0;
    margin: 0;
    overflow: hidden;
    border: none;
    color: $N01;
    clip: auto;
    white-space: normal;
    &:focus {
      width: auto;
      height: auto;
      padding: 9px 10px;
      border: 1px solid;
      display: block;
      top: 0;
      left: 0;
      z-index: 9999999;
      outline: none;
    }
  }
}

//Import all plugins
@import "plugins";
@import "animation";

#wrapper {
  max-width: 2560px;
  margin-left: auto;
  margin-right: auto;
}

//Sitefinity Admin panel fixes
[class*="sf-"] table,
.sfPageEditor table,
.rwTable,
.rdTable,
.sfWrapper,
.sfPageEditor.page,
.sfPageEditor {
  visibility: visible !important;
  ol,
  ul,
  table,
  iframe,
  a[href^="http"] {
    visibility: visible !important;
  }
  .page {
    line-height: 1.25;
  }
}

/* clears the ‘X’ from Internet Explorer */
input[type="search"]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type="search"]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

/* clears the ‘X’ from Chrome */
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

.sfPageEditor {
  .rdTable tr td {
    .media,
    .spotlight:not(.spotlight-testimony) {
      max-width: 540px !important;
    }
    .spotlight-card {
      max-width: 360px;
    }
    .row > div {
      width: 100%;
    }
    *[class*="-none"] {
      display: block !important;
    }
  }
  .row .rdTable tr td .rdContent {
    @extend .row;
  }
  //override checkbox, radio button and select styles
  &.modal-open {
    .sf-backend-wrp .modal-dialog .modal-body #viewsPlaceholder {
      .form-group.ng-scope {
        border-bottom: 1px solid transparent !important;
        .form-group {
          .form-control[type="radio"],
          .form-control[type="checkbox"] {
            width: auto !important;
            height: auto !important;
          }
        }
        select {
          border: 1px solid #cccccc!important;
          width: auto !important;
          height: auto !important;
          padding: 5px 10px !important;
        }
      }
    }
  }
}

button[type="button"],
button[type="submit"],
input[type="button"],
input[type="submit"],
.btn,
a {
  &:focus {
    outline: thin dotted;
  }
}

//its for testing only and will be removed.
.localhost-x {
  a {
    &:before {
      background-color: red !important;
      color: $N01 !important;
      content: "I am link" !important;
    }
  }
  .nav.nav-stacked a,
  #sidebar a,
  a[class*="btn"],
  a[class*="spotlight"],
  .download-link,
  .card-link,
  .cta-link,
  .pagination a,
  .sidebar a,
  .breadcrumb a,
  header a,
  footer a {
    &:before {
      background-color: transparent !important;
      content: "" !important;
    }
  }
}