.strapline-wrapper {
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  display: flex;
  flex-direction: row;
  @media screen and (max-width: $screen-xs) {
    flex-direction: column;
  }
  .column-left {
    display: flex;
    flex-direction: row;
  }
  .button-box {
    display: flex;
    align-items: center;
    @media screen and (min-width: $screen-sm) {
      margin-left: 50px;
    }
    .btn {
      white-space: nowrap;
      @media screen and (max-width: $screen-xs) {
        width: 100%;
      }
    }
  }
  .icon-strapline {
    margin-right: 50px;
    @media screen and (max-width: $screen-xs) {
      margin-right: 5px;
    }
  }
  .title {
    margin-bottom: 10px;
    display: block;
    @include fsB03;
    color: $P01;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B03;
    }
  }
}

.notification-banner {
  padding: 30px 0;
  margin: 0;
  width: 100%;
  display: block;
  .container {
    position: relative;
  }
  .media-column {
    @media screen and (min-width: $screen-sm) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    img {
      margin-bottom: 0;
      @media screen and (max-width: $screen-xs) {
        margin-top: 8px;
      }
    }
  }
  .content-column {
    .h3 {
      color: $P01;
      display: block;
      margin-bottom: 10px;
    }
    p {
      margin-bottom: 0;
    }
  }
  .close {
    opacity: 1;
    color: $N03;
    width: 48px;
    height: 48px;
    span {
      font-size: 0;
      @include svg-close-grey;
      display: block;
      margin: auto;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      position: absolute;
    }
    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}

.search-banner {
  background: $S01;
  position: relative;
  &:before {
    content: "";
    background: url("imgs/search-bg.png") no-repeat center center;
    display: block;
    margin: auto;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    @media screen and (max-width: $screen-xs) {
      background: url("imgs/search-bg-mobile.png") no-repeat bottom center;
      background-size: cover;
    }
  }
  .container {
    position: relative;
    z-index: 2;
  }
  h1 {
    @include fsB02;
    color: $S01;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B02;
    }
  }
}

.search-box {
  background-color: $N01;
  border-radius: 10px;
  min-height: 88px;
  position: relative;
  @media screen and (max-width: $screen-xs) {
    min-height: 55px;
  }
  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    display: block;
    margin: auto;
  }
  &:before {
    right: auto;
    left: 40px;
    width: 3px;
    height: 33px;
    background-color: $S01;
    @media screen and (max-width: $screen-xs) {
      height: 23px;
      left: 20px;
    }
  }
  &:after {
    @include svg-search;
    right: 40px;
    left: auto;
    @media screen and (max-width: $screen-xs) {
      right: 20px;
      transform: scale(0.6);
    }
  }
  .form-control,
  .btn-search,
  .input-group-append {
    background-color: transparent;
  }
  .form-control {
    height: 88px;
    padding: 15px 30px 15px 53px;
    border-color: transparent;
    @include fsM03;
    @media screen and (max-width: $screen-xs) {
      height: 55px;
      padding: 15px 15px 15px 25px;
    }
    &:hover,
    &:focus {
      border-color: transparent;
      box-shadow: none;
      outline: none;
    }
    @include placeHolderTextColor($N03);
  }
  .btn-search {
    width: 88px;
    height: 88px;
    border: none;
    box-shadow: none;
    @media screen and (max-width: $screen-xs) {
      height: 55px;
      width: 55px;
    }
  }
}


.page-404 {
  padding: 0;
  .container {
    min-height: 1183px;
    padding-top: 300px;
    background: url("imgs/bg-wheel.png") no-repeat center right;
    @media screen and (max-width: $screen-xs) {
      padding-top: 200px;
      background-size: contain;
    }
  }
}

.bg-image {
  .trustpilot-widget {
    @media screen and (max-width: $screen-xs) {
      margin-top: 110px;
      max-width: 170px;
      max-height: 100px;
    }
  }
}