section {
  padding-top: 60px;
  padding-bottom: 30px;
  &.breadcrumb-section {
    padding-top: 0;
    padding-bottom: 0;
    .breadcrumb {
      background-color: transparent;
    }
  }
  @media screen and (max-width: $screen-xs) {
    padding-top: 30px;
    padding-bottom: 15px;
  }
  &.bg-light + .bg-image {
    @media screen and (max-width: $screen-xs) {
      padding-top: 0;
    }
  }
  + aside {
    padding-bottom: 0;
    @media screen and (max-width: $screen-xs) {
      padding-bottom: 0;
    }
  }

}
aside {
  padding-top: 60px;
  padding-bottom: 60px;
  @media screen and (max-width: $screen-xs) {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
img,
figure,
ol:not(.breadcrumb),
ul,
ol,
table,
.table-wrapper,
.download-link,
.standard-content a[class*="btn-"],
.embed-responsive,
blockquote,
.search-result,
.search-heading,
.featured-blog-item,
.spotlight,
.spotlight-card,
.product-thumb,
.accordion {
  margin-bottom: 30px;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 15px;
  }
}

.spotlight.with-buttons + .buttons-wrapper {
  margin-bottom: 30px;
}

.search-heading + .search-result {
  padding-top: 30px;
}
