//Anotated text style start
.annotated-text {
  color: $deep-sky-blue;
  text-transform: uppercase;
  min-height: 204px;
  opacity: 1;
  animation: fading 1s;
  white-space: nowrap;
  @media screen and (max-width: $screen-xs) {
    white-space: normal;
  }
  .inner-text {
    transition: 1s;
    color: $white;
    display: block;
    text-transform: none;
  }
}
@keyframes fading {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


//Anotated text style end