.video-wrap {
  .youtube-box,
  .youtube-frame {
    display: block;
    background-size: 100%;
    background-position: 0;
  }
  .youtube-box {
    position: relative;
    border: none;
    cursor: pointer;
    &:after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      margin: auto;
      width: 100%;
      height: 100%;
      pointer-events: none;
      background-color: rgba(0,0,0,0.2);
      z-index: 1;
    }
    .play-icon {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      margin: auto;
      @include svg-play-icon;
      pointer-events: none;
      z-index: 2;
    }
  }
}