.cards-carousel {
  min-height: 450px;
  padding: 80px 0 0;
  overflow: hidden;
  @media screen and (max-width: $screen-xs) {
    padding: 60px 0 0;
    .container {
      padding-right: 0;
    }
  }
  .owl-carousel {
    position: relative;
    top: 0;
    left: 0;
    min-width: calc(((100vw - 1200px) / 2) + 1200px);
    min-height: 450px;
    @media screen and (max-width: $screen-xs) {
      min-width: 1px;
      min-height: 450px;
    }
    .owl-item {
      &.active {
        @media screen and (max-width: $screen-xs) {
          width: calc(100vw - 55px) !important;
          max-width: 360px;
        }
      }
      img {
        display: block;
        width: auto;
        margin: 0;
        min-width: 100%;
        height: 450px;
        @media screen and (max-width: $screen-xs) {
          object-fit: cover;
        }
        @media screen and (min-width: 1921px) {
          object-fit: cover;
        }
      }
    }
    .owl-nav {
      position: absolute;
      top: -70px;
      left: 1073px;
      width: 90px;
      @media screen and (max-width: $screen-xs) {
        top: -50px;
        left: auto;
        right: 0;
      }
      .owl-next,
      .owl-prev {
        position: relative;
        width: 40px;
        height: 40px;
        border: 1px solid transparent;
        margin: 0 1px 0 0;
        &:before {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          display: block;
          margin: auto;
        }
        span {
          display: none !important;
        }
        &:focus {
          outline: thin dotted $N04;
        }
      }
      .owl-next {
        &:before {
          @include svg-arrow-forward;
        }
      }
      .owl-prev {
        &:before {
          @include svg-arrow-back;
        }
      }
    }
  }
}

@import "spot";
@import "variation-style";