h1,
.h1 {
  color: $N06;
  @include fsB01;
  @media screen and (max-width: $screen-xs) {
    @include fsB02;
  }
}

h2,
.h2 {
  color: $N06;
  @include fsB02;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B02;
  }
}

h3,
.h3 {
  @include fsB03;
}

h4,
.h4 {
  @include fsB04;
}

ol:not(.breadcrumb) li,
ul li,
.p-style,
label,
address,
p {
  @include fsR03;
}

p.intro {
  @include fsR01;
  @media screen and (max-width: $screen-xs) {
    @include fsR02;
  }
}

p.lead {
  @include fsR02;
  @media screen and (max-width: $screen-xs) {
    @include fsR03;
  }
}

.overline {
  @include fsR05;
}

.caption {
  @include fsR04;
}

figure figcaption {
  @include fsMobile-R03;
}

table {
  tr {
    th {
      @include fsB04;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B05;
      }
    }
    td {
      font-size: 1.15rem;
      line-height: 1.55;
    }
  }
}

blockquote {
  @include fsB04;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B05;
  }
}

figure figcaption {
  text-align: left;
}

.bg-banner {
  h1,
  .h1 {
    @include fsB02;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B02;
    }
  }
}
