.megamenu-multi-level {
  background-color: $P01;
  * {
    color: $N04;
  }
}
.gns_multi_level .open > a,
.gns_multi_level .show > a {
  @media screen and (min-width: $screen-sm) {
    background-image: url("imgs/megamenu-multi-level/dropdown_triangle.svg");
    background-position: center bottom;
    background-repeat: no-repeat;
  }
}

.multi_level_nav {
  .tns_multi_level {
    background-color: $P03;
  }

  .gns_multi_level {
    @media screen and (max-width: $screen-xs) {
      border-bottom: 1px solid $silver;
    }
    .navbar-default {
      border: none;
    }
    .navbar {
      .navbar-header {
        button {
          @media screen and (max-width: $screen-xs) {
            border: none;
            background-color: transparent;
            border-radius: 0;
            &:hover,
            &:focus,
            &:active {
              background-color: transparent;
              outline: none;
              box-shadow: none;
            }
          }
          .green-burger {
            @include svg-menu;
          }
          .x-icon {
            @include svg-close;
          }
        }
      }
      .navbar-collapse-grid {
        @media screen and (max-width: $screen-xs) {
          border: none;
        }
        ul.nav {
          li {
            > a {
              color: $N01;
              background-color: transparent;
              &:focus {
                outline: dotted 1px !important;
                @media screen and (max-width: $screen-xs) {
                  outline: none !important;
                  text-decoration: none;
                }
              }
            }
            .dropdown-menu {
              @media screen and (min-width: $screen-sm) {
                background : $N01;
              }
              .inner-column {
                background-color: transparent;
                &.right {
                  @media screen and (max-width: $screen-xs) {
                    background-color: $N02;
                  }
                }
                ul li {
                  a {
                    color: $black;
                    text-align: left;
                    &:focus {
                      outline: dotted 1px !important;
                    }
                  }
                  &:after {
                    background: url("imgs/megamenu-multi-level/google_app_store.png") no-repeat;
                  }
                  a {
                    img {
                      z-index: 10;
                    }
                    span {
                      @media screen and (min-width: $screen-sm) {
                        &:after {
                          background-image: url("imgs/megamenu-multi-level/chevron_white.svg");
                          background-repeat: no-repeat;
                          background-size: 80%;
                        }
                      }
                    }
                  }
                }
              }
            }
            &.active-page {
              > a {
                border-bottom: 2px solid $P02;
              }
            }
          }
        }
      }
    }
  }
}

.megamenu-multi-level {
  .bg-trans {
    background-color: transparent !important;
  }

  .minus-icon,
  .plus-icon {
    @media screen and (max-width: $screen-xs) {
      position: relative;
      border-bottom: 2px solid $P03;
      &:before {
        content: "";
        @include svg-arrow-down-white;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        display: block;
        margin: auto;
      }
      &.show {
        border-bottom-color: transparent;
        &:before {
          transform: rotate(180deg);
        }
      }
    }
  }
}