.standard-content {
  img {
    display: block;
    width: auto;
    height: auto;
    max-width: 100%;
    margin: 0 0 15px;
    //if image contains style attribute with float left/right
    &.left,
    &.float-left,
    &[style*="float: left;"] {
      width: auto;
      height: auto;
      float: left;
      margin: 0 30px 30px 0;
      @media screen and (max-width: $screen-xs) {
        float: none;
        display: block;
        margin: 0 0 15px 0;

      }
    }

    &.right,
    &.float-right,
    &[style*="float: right;"] {
      width: auto;
      height: auto;
      float: right;
      margin: 0 0 30px 30px;
      @media screen and (max-width: $screen-xs) {
        float: none;
        display: block;
        margin: 0 0 15px 0;
      }
    }
  }

  figure {
    margin: 0 0 10px;
    figcaption {
      text-align: center;
      font-style: italic;
    }
  }

  dl {
    margin: 0 0 10px;
    dd {
      padding: 0 0 0 10px;
      margin: 0 0 10px;
    }
  }

  meter {
    width: 100%;
    margin: 0 0 10px;
  }

  progress {
    width: 100%;
    margin: 0 0 10px;
  }

  details {
    margin: 0 0 10px;
    summary {
      font-weight: bold;
    }
    p {
      margin-left: 15px;
    }
  }

  blockquote {
    margin: 0 0 30px;
    &:before,
    &:after {
      font-size: 1.25rem;
      content: "";
    }
    small {
      display: block;
      &:before {
        content: "\2014 \00A0";
      }
    }
  }

  iframe {
    display: block;
    visibility: hidden;
    &[data-src*="dotmailer-surveys.com"],
    &[src*="dotmailer-surveys.com"] {
      visibility: visible;
    }
  }

  .embed-responsive {
    iframe {
      visibility: visible;
    }
  }

  iframe.cke_wysiwyg_frame.cke_reset {
    //to make standard content visible in Admin WYSIWYG editor
    visibility: visible;
  }
  a {
    &.btn {
      margin: 0 10px 0 0;
      @media screen and (max-width: $screen-xs) {
        width: 100%;
        margin: 0 0 10px 0;
      }
    }
    &.btn-download {
      span,
      strong {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
  hr {
    clear: both;
  }
}

.standard-content,
section {
  ul {
    list-style: disc;
  }
  ol,
  ul {
    visibility: hidden;
    padding: 0 0 0 11px;
    margin-left: 0;
    margin-right: 0;
    li {
      padding: 0 0 10px;
      &:last-of-type {
        padding-bottom: 0;
      }
    }
    &.wrapped {
      visibility: visible;
      li {
        color: $P01;
        span {
          color: $N04;
          ol,
          ul {
            padding-top: 10px;
            padding-bottom: 0;
            margin-bottom: 0;
          }
        }
      }
    }
  }
  ul.wrapped {
    li {
      span {
        display: inline-block;
        vertical-align: top;
      }
    }
  }

  table {
    visibility: hidden;
  }

  .table-wrapper {
    width: 100%;
    overflow-y: auto;
    padding-bottom: 6px;
    margin: 0 0 20px;
    &::-webkit-scrollbar {
      -webkit-appearance: none;
      appearance: none;
      width: 10px;
      height: 10px;
    }
    &::-webkit-scrollbar-thumb {
      border: 1px solid $N03;
      background-color: $N03;
    }
    table {
      visibility: visible;
      width: 100%;
      margin: 0;
      @media screen and (max-width: $screen-xs) {
        width: auto;
      }
      caption {
        display: none;
      }
      thead {
        background-color: $N04;
        tr {
          th {
            color: $white;
            border: none;
          }
        }
      }
      tbody {
        tr {
          td {
            border: none;
          }
        }
      }
    }
  }
}

.sidebar {
  background-color: transparent;
  padding: 15px 10px;
  h3 {
    display: block;
    padding: 5px 0;
    @include fsB05;
    margin-bottom: 10px;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    visibility: visible;
    li {
      padding: 0;
      display: block;
      margin-bottom: 10px;
      a {
        display: inline-block;
        @include fsR04;
        color: $N04;
      }
      &.active {
        a {
          color: $P01;
        }
      }
    }
  }
}