.swap-on-mobile {
  @media screen and (max-width: $screen-xs) {
    @include swapItems(".mobile-first", ".mobile-second");
    .mobile-second {
      padding-top: 15px;
    }
    .mobile-third {
      -webkit-box-ordinal-group: 3;
      -moz-box-ordinal-group: 3;
      -ms-flex-order: 3;
      -webkit-order: 3;
      order: 3;
      padding-top: 15px;
      padding-bottom: 25px;
    }
  }
}