.bg-g01 {
  background: $G01;
}
.bg-g02 {
  background: $G02;
}
.bg-g03 {
  background: $G03;
}
.bg-g04 {
  background: $G04;
}

.bg-light {
  background-color: $N02 !important;
}

.bg-image {
  min-height: 536px;
  @media screen and (min-width: $screen-sm) {
    background: url("imgs/trust-pilot-bg.jpg") no-repeat center center;
    background-size: cover;
  }
  @media screen and (max-width: $screen-xs) {
    min-height: 520px;
    padding-left: 15px;
    padding-right: 15px;
    //width: calc(100% - 30px);
    //max-width: 480px;
    //margin: 15px auto;
    background-color: $N02;
  }
  .container {
    @media screen and (max-width: $screen-xs) {
      box-shadow: 1px 4px 11px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      background: url("imgs/tp-mobile-bg.png") no-repeat top right $N01;
    }
  }
}

.bg-pattern {
  @media screen and (min-width: $screen-sm) {
    padding: 60px 0;
    background: url("imgs/bg-bubbles.png") no-repeat center top;
  }
  @media screen and (max-width: $screen-xs) {
    background-color: $N01;
  }
}

.bg-banner {
  background: url("imgs/bg-yellow-circle.png") no-repeat right top;
  @media screen and (max-width: $screen-xs) {
    background-size: contain;
    background-position: 0 -35px;
  }
}
