.megamenu-multi-level {
  position: relative;
  display: block;
  clear: both;
  .logo-link {
    width: 180px;
    height: 60px;
    max-width: 180px;
    float: left;
    @media screen and (min-width: $screen-sm) {
      margin-top: 30px;
    }
    @media screen and (max-width: $screen-xs) {
      width: 90px;
      height: 30px;
    }
    img {
      display: block;
      width: 100%;
      height: auto;
      margin: 0;
    }
  }
}

.gns_multi_level {
  .logo-cell {
    @media screen and (max-width: $screen-xs) {
      position: absolute;
    }
  }
  .open > a {
    @media screen and (min-width: $screen-sm) {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }
  .nav > li > a {
    position: relative;
  }
}

.multi_level_nav {
  .tns_multi_level {
    position: relative;
    width: 100%;
    clear: both;
    padding: 18px 0;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
    .tns_multi_level_left {
      ul {
        display: block;
        padding: 0;
        margin: 0;
        text-align: right;
        li {
          display: inline-block;
          padding: 0 20px;
          position: relative;
          &:after {
            content: "";
            width: 1px;
            height: 19px;
            background-color: $N01;
            display: block;
            margin: auto;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: auto;
          }
          a:not(.btn) {
            @include fsR04;
            color: $N01;
          }
          &:first-of-type {
            padding-left: 0;
          }
          &:last-of-type {
            padding-right: 0;
            &:after {
              opacity: 0;
            }
          }
        }
      }
    }
  }

  .gns_multi_level {
    @media screen and (max-width: $screen-xs) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .navbar {
      position: static;
      padding-top: 0;
      padding-bottom: 0;
      .navbar-header {
        @media screen and (max-width: $screen-xs) {
          width: 100%;
        }
        button {
          @media screen and (max-width: $screen-xs) {
            float: right;
            width: 40px;
            height: 30px;
            position: relative;
            .green-burger {
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              left: auto;
              display: block;
              margin: auto;
            }
          }
        }
      }
      .navbar-collapse-grid {
        position: static;
        @media screen and (min-width: $screen-sm) {
          float: right;
        }
        @media screen and (max-width: $screen-xs) {
          margin: 0 -15px;
          padding-top: 15px;
        }
        ul.nav {
          position: static;
          flex-direction: row;
          float: right;
          @media screen and (max-width: $screen-xs) {
            float: none;
            width: 100%;
          }
          > li {
            a.dropdown-toggle {
              position: relative;
              float: left;
              &:after {
                display: none;
              }
              @media screen and (max-width: $screen-xs) {
                float: none;
                width: 100%;
                display: inline-block;
              }
            }
          }
          li {
            float: left;
            display: block;
            @media screen and (max-width: $screen-xs) {
              float: none;
              width: 100%;
            }
            &.dropdown {
              position: static;
              @media screen and (max-width: $screen-xs) {
                padding: 10px 15px;
              }
              @media screen and (min-width: $screen-sm) {
                padding: 37px 15px 65px;
                &:first-of-type {
                  padding-left: 0;
                }
                &:last-of-type {
                  padding-right: 0;
                }
              }
              > a {
                @include fsB04;
                color: $N01;
                @media screen and (max-width: $screen-xs) {
                  padding: 0 0 5px;
                  @include fsMobile-B03;
                }
              }
              &:last-of-type {
                @media screen and (max-width: $screen-xs) {
                  a {
                    border-bottom-color: transparent;
                  }
                }
              }
            }
            .dropdown-menu {
              left: 0;
              right: 0;
              top: 194px;
              position: absolute;
              border: none !important;
              padding: 0;
              @media screen and (min-width: $screen-sm) {
                @include dropShadowMainBanner;
              }
              @media screen and (max-width: $screen-xs) {
                background-color: $N01;
                position: static;
                border: none;
                border-radius: 0;
                margin: 0 -16px;
              }

              .container {
                @media screen and (min-width: $screen-sm) {
                  position: relative;
                  min-height: 475px;
                  padding-top: 0;
                  padding-bottom: 50px;
                  &:before {
                    content: "";
                    background: url("imgs/megamenu-multi-level/menu-curve.png") no-repeat center center;
                    width: 1035px;
                    height: 475px;
                    z-index: 1;
                    position: absolute;
                    top: 0;
                    right: 850px;
                    bottom: 0;
                    left: auto;
                    display: block;
                    margin: auto;
                  }
                }
              }
              .inner-row {
                min-width: 100%;
                @media screen and (max-width: $screen-xs) {
                  @include swapItems(".left.inner-column", ".right.inner-column");
                }
              }
              .inner-column {
                position: relative;
                z-index: 2;
                @media screen and (min-width: $screen-sm) {
                  min-height: 300px;
                }
                @media screen and (max-width: $screen-xs) {
                  width: 100%;
                  padding-top: 30px;
                  padding-bottom: 30px;
                }
                ul {
                  padding: 0;
                  margin: 0;
                  list-style: none;
                  li {
                    display: inline-block;
                    width: 100%;
                  }
                }
                ul.ul-cols {
                  width: 100%;
                  @media screen and (min-width: $screen-sm) {
                    display: flex;
                    flex-direction: row;
                  }
                  > li {
                    @media screen and (min-width: $screen-sm) {
                      position: relative;
                      padding: 0 30px 0 50px;
                      &:before {
                        content: "";
                        width: 2px;
                        height: 140px;
                        background-color: $N02;
                        position: absolute;
                        top: 0;
                        right: 27px;
                        bottom: 0;
                        left: auto;
                        display: block;
                        margin: auto;
                      }
                      &:last-of-type:before {
                        opacity: 0;
                      }
                    }
                    .h4 {
                      @include fsB04;
                      margin-bottom: 20px;
                      @media screen and (max-width: $screen-xs) {
                        @include fsMobile-B05;
                      }
                    }
                    @media screen and (min-width: $screen-sm) {
                      display: flex;
                      flex-direction: column;
                    }
                    &:focus {
                      outline: none;
                    }
                    > span,
                    > ul {
                      display: block;
                      @media screen and (max-width: $screen-xs) {
                        margin-bottom: 30px;
                      }
                      li {
                        padding: 10px 0;
                        &:first-of-type {
                          padding-top: 0;
                        }
                        &:last-of-type {
                          padding-bottom: 0;
                        }
                      }
                    }
                    &:last-of-type > ul {
                      @media screen and (max-width: $screen-xs) {
                        margin-bottom: 0;
                      }
                    }
                  }
                }
                .menu-card {
                  padding: 40px 20px;
                  background-color: $N01;
                  float: left;
                  border-radius: 4px;
                  display: block;
                  width: 100%;
                  @include dropShadow;
                  @media screen and (min-width: $screen-sm) {
                    min-height: 265px;
                    width: 238px;
                    max-width: 238px;
                  }
                  @media screen and (max-width: $screen-xs) {
                    margin: 15px auto;
                  }
                  img {
                    height: 240px;
                    width: 240px;
                    position: absolute;
                    margin: 0;
                  }
                  .intro {
                    @include fsR04;
                    display: block;
                    margin-bottom: 30px;
                    @media screen and (max-width: $screen-xs) {
                      display: none;
                    }
                  }
                  .h4 {
                    @include fsB04;
                    color: $S01;
                    margin-bottom: 10px;
                    display: block;
                  }
                  a {
                    margin-top: 40px;
                  }
                }
                &.right {
                  padding: 0 25px;
                  @media screen and (max-width: $screen-xs) {
                    background-color: $N02;
                    //padding: 15px 15px;
                    padding: 0 15px;
                  }
                }
                &.left {
                  @media screen and (min-width: $screen-sm) {
                    padding-top: 17px;
                    padding-left: 45px;
                  }
                }
              }
            }
          }
        }
        @media screen and (max-width: $screen-xs) {
          .tns_multi_level_left {
            float: left;
            width: 100%;
            margin-bottom: -10px;
            ul {
              padding: 40px 0;
              margin: 0;
              float: left;
              width: 100%;
              list-style: none;
              li {
                padding: 12px 15px;
                float: left;
                width: 100%;
                a:not(.btn-quote) {
                  @include fsMobile-R03;
                  color: $N01;
                }
              }
            }
          }
        }
      }
    }
  }
}
