.cards-carousel.desktop-carousel {
  .owl-carousel {
    min-width: 100%;
    .owl-dots {
      text-align: center;
      margin-top: 30px;
      .owl-dot {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        margin-right: 10px;
        background-color: transparent;
        border: 1px solid $silver;
        &.active {
          border: 1px solid $black;
        }
      }
    }
    @media screen and (max-width: $screen-xs) {
      .owl-stage-outer {
        height: auto !important;
        .owl-stage {
          width: 100%;
          max-width: 100%;
          transform: none !important;
          .owl-item {
            display: none;
            &.active {
              display: block !important;
              float: none !important;
              width: 100% !important;
              margin-right: 0 !important;
              margin-bottom: 30px;
            }
          }
        }
      }
      .owl-nav,
      .owl-dots {
        display: none !important;
      }
    }
  }
}