/*
Buttons  */
.btn {
  @media screen and (max-width: $screen-xs) {
    padding: 15px 35px;
  }
  &:hover {
    box-shadow: none;
  }
  &:not(:disabled):not(.disabled) {
    &:focus,
    &:active {
      box-shadow: none;
    }
  }
  &.btn-primary {
    @include fsB07;
    color: $N01;
    @media screen and (max-width: $screen-xs) {
      @include fsB06;
    }
    &:hover {
      text-decoration: none;
      background-color: transparent;
      border-color: $P01;
      color: $P01;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        text-decoration: none;
        background-color: transparent;
        border-color: $P01;
        color: $P01;
      }
    }
  }
  &.btn-secondary {
    @include fsB07;
    color: $N04;
    &:hover {
      color: $N04;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        color: $N04;
      }
    }
  }
  &.btn-outline-white {
    padding: 15px 35px;
    color: $N01;
    border-color: $N01;
    &:hover {
      text-decoration: none;
      background-color: transparent;
      color: $N01;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        text-decoration: none;
        background-color: transparent;
        color: $N01;
      }
    }
  }
  &.btn-quote {
    @include fsR04;
    border: none;
    color: $N04;
    background-color: $P02;
    padding: 10px 15px;
    @media screen and (max-width: $screen-xs) {
      font-weight: 500;
    }
    &:after {
      content: "";
      @include svg-arrow-right;
      display: inline-block;
      margin-left: 5px;
      margin-bottom: -1px;
    }
    &:hover {
      text-decoration: underline;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        text-decoration: underline;
      }
    }
  }
  &.btn-link {
    @include fsB07;
    color: $N01;
    border-color: $N01;
    @media screen and (max-width: $screen-xs) {
      @include fsB06;
    }
    &:hover {
      background-color: $N01;
      color: $P01;
      text-decoration: none;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        background-color: $N01;
        color: $P01;
        text-decoration: none;
      }
    }
  }
  &.btn-outline-dark {
    @include fsB07;
    @media screen and (max-width: $screen-xs) {
      @include fsB06;
    }
    &:focus {
      color: $N01;
      background-color: $N04;
      border-color: $N04;
      outline: none;
    }
  }
  &.btn-collapse {
    padding: 10px 10px;
    display: inline-block;
    width: 100%;
    position: relative;
    &:after {
      content: "\002b";
      position: absolute;
      top: 0;
      right: 10px;
      bottom: 0;
      left: auto;
      display: block;
      margin: auto;
    }
  }

  &.external-link {
    &:after {
      content: "";
      display: inline-block;
      margin-left: 15px;
      margin-bottom: -2px;
    }
    &.btn-outline-white {
      &:after {
        @include svg-external-link-white;
      }
      &:hover {
        &:after {
          @include svg-external-link-white;
        }
      }
      &:not(:disabled):not(.disabled) {
        &:focus,
        &:active {
          &:after {
            @include svg-external-link-white;
          }
        }
      }
    }
    &.btn-link,
    &.btn-primary {
      &:after {
        @include svg-external-link-white;
      }
      &:hover {
        &:after {
          @include svg-external-link-green;
        }
      }
      &:not(:disabled):not(.disabled) {
        &:focus,
        &:active {
          &:after {
            @include svg-external-link-green;
          }
        }
      }
    }
    &.btn-secondary,
    &.btn-quote {
      &:after {
        @include svg-external-link-black;
      }
    }
    &.btn-outline-dark {
      &:after {
        @include svg-external-link-black;
      }
      &:hover {
        &:after {
          @include svg-external-link-white;
        }
      }
      &:not(:disabled):not(.disabled) {
        &:focus,
        &:active {
          &:after {
            @include svg-external-link-white;
          }
        }
      }
    }
  }
}